const utils = require('./utils.js');

function collabseCratesChances() {
  var viewport = utils.findBootstrapEnvironment();
  if (viewport == "xs" || viewport == "sm") {
    $('.crates-chances-table').removeClass('in');
  } else {
    $('.crates-chances-table').addClass('in');
  }
}

function insertAdClerksAds() {
  if ($("#ads").length) {
    function injectAdDiv(IDs) {
      var ad = $('<div/>', {'id': IDs[0], 'class': 'ad'})
      .append(
        $('<p/>', {'class': 'ad-help-text'})
        .append(
          $('<a/>', {'href': 'http://adclerks.com/marketplace/website/8954/wwe-champions-guide', 'target': '_blank', 'text': 'Advertise here for as low as $0.1 NOW! Factions ads are welcomed.'})
        )
        .append(
          $('<div/>', {'id': IDs[1]})
        )
      );
      $('#ads').append(ad);
    }

    var adURL = '';
    var adDivIDs = [];
    var viewport = utils.findBootstrapEnvironment();
    if (viewport != "xs") {
      adURL = "/js/ads/728_90_header.js";
      adDivIDs = ['adLeaderboard', 'pubclerks_3785'];
    } else {
      adURL = "/js/ads/320_50_header.js";
      adDivIDs = ['adMobileLeaderboard', 'pubclerks_3786'];
    }

    injectAdDiv(adDivIDs);
    $.getScript(adURL);
  }
}

// Function called if AdBlock is not detected
function adBlockNotDetected() {
  console.log('AdBlock is not enabled');
  $('#adBlocked')
  .removeClass('alert')
  .removeClass('alert-danger')
  .attr('role', null)
  .empty();
}

// Function called if AdBlock is detected
function adBlockDetected() {
  console.log('AdBlock is enabled');
  $('#adBlocked')
  .addClass('alert')
  .addClass('alert-danger')
  .attr('role', 'alert')
  .append(
    $('<p/>', {
      'class': 'text-center',
      'text': 'To keep the website updated frequently, please help support it by whitelisting it on your ad blocker. Thank you!'
    })
  );
}

function insertAds() {
  insertAdClerksAds();
  adblockDetect(function(adblockDetected){
    if (adblockDetected) {
      adBlockDetected();
    } else {
      adBlockNotDetected();
    }
  });
}

function buildRelativeDateString(dateDiff) {
  var result = [];
  if (dateDiff.years) {
    result.push(dateDiff.years + 'y');
  }
  if (dateDiff.months) {
    result.push(dateDiff.months + 'm');
  }
  if (dateDiff.days) {
    result.push(dateDiff.days + 'd');
  }
  if (dateDiff.hours) {
    result.push(dateDiff.hours + 'h');
  }
  if (dateDiff.minutes) {
    result.push(dateDiff.minutes + 'm');
  }
  var dateString = result.join(" ");
  if (dateDiff.firstDateWasLater) {
    dateString = 'in ' + dateString;
  } else {
    dateString = dateString + ' ago';
  }
  return dateString;
}

function checkExpiredEvents() {
  if ($('.panel-events').length) {
    console.log('There are events!');
    $('.panel-events .panel').each(function(){
      var startTime = moment($(this).find('time.start-time').first().attr('datetime'));
      var endTime = moment($(this).find('time.end-time').first().attr('datetime'));
      var currentDatetime = moment();
      var soonStartDatetime = moment().add(1, 'day');
      if (currentDatetime >= endTime) {
        if ($(this).find('time.title-time').length) {
          $(this).find('time.title-time').first().text('Expired!');
        } else {
          $(this).find('.panel-title a').first().text('Expired!');
        }
        $(this).removeClass('panel-default').addClass('panel-danger');
      } else if (currentDatetime >= startTime && currentDatetime < endTime) {
        $(this).removeClass('panel-default').addClass('panel-success');
      } else if (startTime <= soonStartDatetime) {
        $(this).removeClass('panel-default').addClass('panel-warning');
      }
    });
  }
}

$(document).ready(function () {
  insertAds();
  collabseCratesChances();

  $('[data-toggle="offcanvas"]').click(function () {
    $('.row-offcanvas').toggleClass('active')
  });

  $('[data-toggle="popover"]').popover({
    animation: false
  });

  $('a[data-toggle="popover"]').on('click',function(e){
     e.preventDefault();
     return false;
   });

  $('a.date-time time, a.date-time time.relative, time.relative').each(function(){
    var publishedDatetime = moment($(this).attr('datetime'));
    var currentDatetime = moment();
    var dateDiff = moment(publishedDatetime).preciseDiff(currentDatetime, true);
    var dateString = buildRelativeDateString(dateDiff);
    $(this).text(dateString);
  });

  checkExpiredEvents();

  $(document).on('touchstart click', function (e) {
      $('[data-toggle="popover"],[data-original-title]').each(function () {
          //the 'is' for buttons that trigger popups
          //the 'has' for icons within a button that triggers a popup
          if (!$(this).is(e.target) && $(this).has(e.target).length === 0 && $('.popover').has(e.target).length === 0) {
              (($(this).popover('hide').data('bs.popover')||{}).inState||{}).click = false  // fix for BS 3.3.6
          }

      });
  });

});



